@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
body {
  font-family: "Roboto", sans-serif;
  color: white;
  background-color: black;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
  min-height: 100%;
  min-width: 100%; }
  @media (min-width: 577px) {
    body {
      background-position: center center; } }

.modal-body {
  color: black; }

.bg-dark {
  background-color: rgba(0, 0, 0, 0.95) !important; }

@media screen and (min-width: 768px) {
  .navbar-collapse {
    justify-content: flex-end; } }

.navbar-brand img {
  height: auto;
  width: 100px; }
  @media screen and (min-width: 768px) {
    .navbar-brand img {
      width: 150px; } }

.navbar-nav {
  justify-content: space-between; }
  @media screen and (min-width: 768px) {
    .navbar-nav {
      width: 600px; } }

.navbar-collapse .navbar-nav .nav-link {
  color: rgba(255, 255, 255, 0.7);
  line-height: 35px;
  text-align: center;
  text-transform: uppercase; }

.navbar-collapse .navbar-nav .nav-link:hover,
.navbar-collapse .navbar-nav .nav-link.active {
  color: white;
  border-bottom: 1px solid white; }

.principal-main,
.principal-portfolio,
.footer {
  padding-top: 50px;
  padding-bottom: 50px; }

.principal-main,
.principal-portfolio {
  margin-bottom: 30px; }

.principal-main {
  font-size: 16px; }
  .principal-main h2 {
    font-size: 2em;
    font-weight: 700; }
  .principal-main h6 {
    color: black;
    font-size: 1.13em;
    line-height: 31px;
    text-align: right; }
    .principal-main h6 span {
      background: white;
      padding: 5px 3px; }

@media screen and (min-width: 768px) {
  .principal-main {
    font-size: 22px; }
    .principal-main h6 {
      line-height: 48px; }
      .principal-main h6 span {
        padding: 10px; } }

.principal-portfolio {
  background: black; }
  .principal-portfolio h3 {
    font-weight: 700; }

.footer .col {
  align-items: center;
  display: flex;
  justify-content: space-around; }

.footer .footer-social {
  display: flex; }
  .footer .footer-social div {
    border: 3px solid white;
    border-radius: 50%;
    text-align: center; }
    .footer .footer-social div .fa-facebook-f {
      padding: 10px 13px; }
    .footer .footer-social div .fa-twitter {
      padding: 10px 10px; }
    .footer .footer-social div .fa-instagram {
      padding: 10px 11px; }

.about-title {
  border-bottom: 1px solid white;
  font-size: 24px;
  margin-bottom: 20px;
  padding-bottom: 5px; }

.about-article {
  font-size: 16px; }

.about-founder {
  height: 150px;
  width: 150px;
  overflow: hidden;
  position: relative; }
  .about-founder img {
    position: absolute;
    top: -20px;
    width: 150px; }
  .about-founder span {
    background: black;
    bottom: 0;
    color: white;
    font-size: 16px;
    left: 0;
    position: absolute;
    text-align: center;
    width: 100%; }

@media screen and (min-width: 768px) {
  .about-title {
    font-size: 36px; }
  .about-article {
    font-size: 24px; }
  .about-founder {
    height: 300px;
    width: 300px; }
    .about-founder img {
      width: 300px; }
    .about-founder span {
      font-size: 24px; } }

.portfolio {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.portfolio-title {
  border-bottom: 1px solid white;
  font-size: 24px;
  margin-bottom: 20px;
  padding-bottom: 5px; }

@media screen and (min-width: 768px) {
  .portfolio-title {
    font-size: 36px; } }

.song {
  background-color: white;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 10px;
  overflow: hidden;
  padding: 5px;
  position: relative;
  width: 90px; }

.song-image {
  width: 100%;
  height: 100%; }

.song-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: white;
  bottom: -90px;
  color: black;
  height: 90px;
  position: absolute;
  width: calc(100% - 10px);
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out; }

.song-content span {
  display: block;
  text-align: center;
  font-size: 14px; }

.song-content span:last-child {
  color: gray;
  font-size: 12px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }

.song:hover .song-content,
.song-image:hover .song-content {
  -webkit-transform: translate(0, -90px);
          transform: translate(0, -90px); }

@media screen and (min-width: 768px) {
  .song {
    width: 125px; }
  .song-content {
    bottom: -125px;
    height: 125px; }
  .song:hover .song-content,
  .song-image:hover .song-content {
    -webkit-transform: translate(0, -125px);
            transform: translate(0, -125px); } }

@media screen and (min-width: 992px) {
  .song {
    width: 170px; }
  .song-content {
    bottom: -80px;
    height: 80px; }
  .song:hover .song-content,
  .song-image:hover .song-content {
    -webkit-transform: translate(0, -80px);
            transform: translate(0, -80px); } }

.equipo {
  display: flex;
  flex-direction: column;
  font-size: 16px;
  margin-bottom: 20px; }

.equipo-icon,
.equipo-subtitle {
  align-self: center; }

.equipo-icon {
  background: #880202;
  border-radius: 50%;
  font-size: 22px;
  height: 50px;
  padding: 9px 14px;
  width: 50px; }
  .equipo-icon.mic {
    padding: 9px 18px; }

.equipo-title {
  border-bottom: 1px solid white;
  font-size: 24px;
  margin-bottom: 20px;
  padding-bottom: 5px; }

@media screen and (min-width: 768px) {
  .equipo-title {
    font-size: 36px; } }

.equipo-subtitle {
  font-size: 1.125em; }

.equipo-list {
  list-style-type: none;
  margin: 0;
  padding: 0px; }
  .equipo-list li {
    font-size: 1em;
    margin-bottom: 5px; }
  .equipo-list li span {
    display: block; }
  .equipo-list li span:last-child {
    font-size: 0.875em; }

@media screen and (min-width: 768px) {
  .equipo {
    font-size: 18px; } }

@media screen and (min-width: 992px) {
  .equipo {
    font-size: 22px; } }

label {
  color: white; }

.contacto-title {
  border-bottom: 1px solid white;
  font-size: 24px;
  margin-bottom: 20px;
  padding-bottom: 5px; }

@media screen and (min-width: 768px) {
  .contacto-title {
    font-size: 36px; } }

.songActions {
  text-align: right; }
  .songActions button {
    margin-left: 20px; }

.songForm small {
  color: red; }

.songList {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.songItem {
  background: rgba(0, 0, 0, 0.4);
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 10px;
  width: 100%; }
  .songItem img {
    background: white;
    height: 78px;
    width: 78px; }
  .songItem .songInfo {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 150px; }
    .songItem .songInfo span,
    .songItem .songInfo a {
      display: block; }
    .songItem .songInfo .info-artist,
    .songItem .songInfo .info-album,
    .songItem .songInfo a {
      font-size: 14px; }
    .songItem .songInfo a {
      color: white; }
  .songItem .songAction {
    width: 44px;
    display: flex;
    flex-direction: column;
    justify-content: space-between; }

@media screen and (min-width: 768px) {
  .songItem {
    width: 320px; } }

.feather {
  width: 18px;
  height: 18px;
  stroke: currentColor;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  fill: none; }

